<template>
    <section id="teaser" class="hero has-image is-fullheight"
             :style="{ 'background-image': 'linear-gradient(to bottom, rgba(26, 83, 92, 0.4) 0%,rgba(26, 83, 92, 0.4) 100%), url(' + currentTeaser.image + ')' }">
        <div class="hero-body">
            <div class="container">
               <div class="columns">
                   <div class="column">
                       <div class="teaser-quote">
                           <h1 class="title is-1 has-text-white is-serif">
                               «{{currentTeaser.title}}»
                           </h1>
                           <h2 class="subtitle has-text-white">
                               {{currentTeaser.subtitle}}
                           </h2>
                       </div>

                       <div class="button-scroll">
                           <a class="button is-outlined is-light is-rounded " href="#map" v-smooth-scroll>
                    <span class="icon is-small">
                        <i class="material-icons">expand_more</i></span>
                           </a>
                       </div>
                   </div>
               </div>
            </div>
        </div>
    </section>
</template>

<script>
    export default {
        name: "Teaser",
        data() {
            return {
                canShow: true,
                teasers: [
                    {title: 'Perdeu o seu animal?', subtitle: 'Insira na nossa plataforma os dados do seu animal.', image: 'https://images.unsplash.com/photo-1426287658398-5a912ce1ed0a?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=d2ca73a2ef25eff85533a5efb4860f36&auto=format&fit=crop&w=1352&q=80'},
                    {title: 'Encontrou um animal?', subtitle: 'Insira na nossa plataforma os dados do animal que encontrou.', image: 'https://images.unsplash.com/photo-1444212477490-ca407925329e?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=86d24240ca6b1df611e98ed6bd7a1efc&auto=format&fit=crop&w=1100&q=80'},
                    {title: 'O seu animal está doente?', subtitle: 'Procure veterinários perto de si.', image: 'https://images.unsplash.com/photo-1495528277160-614167d04a0f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=0ffe232f89f37b93dd6498cbed58ed70&auto=format&fit=crop&w=1377&q=80'},
                    {title: 'Vai de férias?', subtitle: 'Procure hóteis ou pet sitters perto de si.', image: 'https://images.unsplash.com/photo-1489440543286-a69330151c0b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=ee90b19b1ca7935b2e49e5b4f0b61c0b&auto=format&fit=crop&w=1350&q=80'},
                    {title: 'Procura eventos para os seus animais?', subtitle: 'Procure eventos de animais perto de si.', image: 'https://images.unsplash.com/photo-1494947665470-20322015e3a8?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=8e64be3b9dcb29b57f6034eeccc6561e&auto=format&fit=crop&w=1350&q=80'}
                ],
                currentTeaser: {
                    title: '«Perdeu o seu animal?»',
                    subtitle: 'Insira rapidamente na nossa plataforma os dados do seu animal.',
                    position: 0,
                    image: 'https://images.unsplash.com/photo-1426287658398-5a912ce1ed0a?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=d2ca73a2ef25eff85533a5efb4860f36&auto=format&fit=crop&w=1352&q=80'
                }
            }
        },
        created() {
            this.animateTeaser()
        },
        methods: {
            animateTeaser: function() {
                const self = this
                setInterval(function() {
                    self.changeTeaser()
                }, 4000);
            },
            changeTeaser: function() {
                if (this.currentTeaser.position < this.teasers.length - 1) {
                    this.currentTeaser.position++
                }
                else {
                    this.currentTeaser.position = 0
                }
                let pos = this.currentTeaser.position

                this.currentTeaser.image = this.teasers[pos].image
                this.currentTeaser.title = this.teasers[pos].title
                this.currentTeaser.subtitle = this.teasers[pos].subtitle
            }
        }
    }
</script>

<style lang="scss">

    @import '../assets/sass/styles';

    .has-image {
        background-position: center center;
        background-repeat:  no-repeat;
        background-attachment: fixed;
        background-size:  cover;
        background-color: #999;
    }

    .is-serif {
        font-family: 'Lora', serif;
    }

    .teaser-quote {
        padding-top: 15%;

    }
