        methods: {
            addMarkers: function() {
                let self = this

                this.points.forEach((each) => {
                    const position = new google.maps.LatLng(each.lat, each.lng);
                    let icon = {}
                    if (each.object_type === 'animal') {
                        if (each.type === 1) {
                            icon = {
                                path: google.maps.SymbolPath.CIRCLE,
                                scale: 8,
                                strokeColor: '#4484F3'
                            }
                        }
                        else if(each.type === 0) {
                            icon = {
                                path: google.maps.SymbolPath.CIRCLE,
                                scale: 8,
                                strokeColor: '#FF6B6B'
                            }
                        }
                    }
                    else if(each.object_type === 'event') {
                        icon = {
                            path: google.maps.SymbolPath.CIRCLE,
                            scale: 8,
                            strokeColor: '#FDC856'
                        }
                    }
                    else if(each.object_type === 'hotel') {
                        icon = {
                            path: google.maps.SymbolPath.CIRCLE,
                            scale: 8,
                            strokeColor: '#317256'
                        }
                    }
                    else if(each.object_type === 'park') {
                        icon = {
                            path: google.maps.SymbolPath.CIRCLE,
                            scale: 8,
                            strokeColor: '#421C52'
                        }
                    }
                    else if(each.object_type === 'vet') {
                        icon = {
                            path: google.maps.SymbolPath.CIRCLE,
                            scale: 8,
                            strokeColor: '#323232'
                        }
                    }
                    const marker = new google.maps.Marker({
                        position,
                        map: self.map,
                        icon
                    });
                    if (!each.type) {
                        self.currentMarkers.push({type: null, marker: marker})
                    }
                    else {

                        self.currentMarkers.push({type: each.type, marker: marker})
                    }
                });
            },
